import wButton from '@/components/watch-login/watch-button.vue' //button
import wPicker from "@/components/w-picker/w-picker.vue";

// 注册,发送验证码方法：
import {register,sendCode} from '@/network/login'

// 判断按钮是否加载中
import {loading} from '@/utils/dealUrl.js'

export default {
	components:{
		wButton,
		wPicker
	},
	data() {
		return {
			windowHeight:0,//屏幕高度
			phone:'',//手机号
			password:'',//密码
			nickname:'',//姓名
			idnum:'',//身份证号
			men:-1,//0男？1女
			isRotate: false, //是否加载旋转
			selectList:[{
				label:"男",
				value:0
			},{
				label:"女",
				value:1
			}],
			sex:'',//男女
			code:'',//验证码,
			isSend:false  ,//判断验证码是否发送
			downTime:60 	,//倒计时
			isLoading:false ,
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	methods: {
		//注册
		startLogin(){
			var that = this
			// 防抖动
			if(that.isRotate) return
			if(!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
				return
			}
			console.log(that.password.length)
			if(that.password.length < 6){
				uni.showToast({
					title:'密码最少为6位',
					icon:'none'
				})
				return
			}
			var regName =/^[\u4e00-\u9fa5]{2,4}$/;  
			if(!regName.test(that.nickname)){
				uni.showToast({
					title:'请输入真实姓名',
					icon:'none'
				})
				return
			}
			if(that.men < 0){
				uni.showToast({
					title:'请选择性别',
					icon:'none'
				})
				return
			}
			let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/;
			if(!(reg.test(this.idnum))){  
				uni.showToast({
					title:'请输入正确的身份证号',
					icon:'none'
				})
			    return false
			}
			that.isRotate = true
			
			setTimeout(function(){
				that.isRotate = false
			},3000)
			
			let data = {
				phone:that.phone,
				pwd:that.password,
				name:that.nickname,
				card:that.idnum,
				sex:that.men,
				code:that.code
			}
			register(data).then(res => {
				if(res.data.code !== 200){
					// 注册有问题
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
				}else{
					// 注册成功,跳转到登录
					uni.navigateTo({
						url:`login?phone=${data.phone}`,
					})
				}
			})
			
		},
		//男女选择开关
		shopTypeClick(){
			this.$refs.picker.show();
		},	
		//选择男女
		onConfirm(val){
			console.log(JSON.stringify(val))
			this.sex= val.result
			this.men = val.checkArr.value
			console.log(this.men)
		},
		codeTap(){
			if(!(/^1[3456789]\d{9}$/.test(this.phone))){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
			}else{
				sendCode(this.phone).then(res => {
					if(res.data.code == 200){
						this.isSend = true
						let t = setInterval(()=> {
							if(this.downTime != 0){
								this.downTime--
							}else{
								clearInterval(t)
								this.isSend =false
								return 
							}
						},1000)
						this.downTime = 60
						
					}else{
						uni.showToast({
							title:'未知错误',
							icon:'none'
						})
					}
				})
			}
			
		}
		
		
	},
	computed:{
		codeText(){
			if(!this.isSend){
				return '发送验证码'
			}else{
				return '等待中'+this.downTime
			}
		}
	}
}
